/* Style for the whole page */
body {
  background-color: #f1f1f1;
  font-family: Arial, sans-serif;
}

/* Style for the main title */
h1 {
  text-align: center;
  margin-top: 40px;
  color: #333;
}

/* Style for the form container */
form {
  max-width: 500px;
  margin: 40px auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

/* Style for the form header */
form > div > h2 {
  color: #333;
  margin-top: 20px;
  margin-bottom: 10px;
  font-size: 18px;
}

/* Style for the radio buttons */
form > div > div {
  margin: 10px 0;
  font-size: 16px;
}

form > div > div > label > input[type="radio"] {
  margin-right: 10px;
}

/* Style for the submit button */
form > div:last-child {
  margin-top: 20px;
  text-align: center;
}

form > div:last-child > input[type="submit"] {
  background-color: #333;
  color: #fff;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}

form > div:last-child > input[type="submit"]:hover {
  background-color: #555;
}